.contact {
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 200px 0 100px;
    align-items: center;
  }
  
  .contact_container {
    border: 2px solid #F9629F;
    width: 600px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px;
    margin-top: 20px;
    border-style: dashed
  }
  
  .contact_container > p {
    font-size: 20px;
    text-align: left;
    padding-bottom: 30px;
  }
  
  .contact_container > p > span {
    color: #F9629F;
  }
  
  